@import "../../../global";

.webplayer-wrapper {
  margin-top: 100px;
  display: grid;
  grid-template-columns: 30% auto;
  grid-gap: 2rem;
  .daftar-materi {
    padding: 1rem;
    background-color: #e7f3e9;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: left;
    border-radius: $radius-button;
    .gambar-thumbnil {
      width: 100%;
      object-fit: cover;
      display: block;
      border-radius: $radius-button;
      height: 180px;
    }
    img{
      position: relative;
      transform: translate(0);
      margin-top: 0;
    }
    .categori-materi {
      color: #163e1f;
      text-transform: uppercase;
      padding-top: 1rem;
      font-weight: 600;
    }
    .judul-materi {
      line-height: 2rem;
      padding: 1rem 0;
    }
    .mentor-materi {
      color: $text-dark;
      font-weight: 500;
    }
    .judul-list {
      padding: 1rem 0;
    }
    .list {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      a {
        text-decoration: none;
        color: $text-dark;
        display: flex;
        align-items: center;
        padding: 8px 0;
        img {
          margin-right: 0.8rem;
        }
      }
    }
  }
  .materi {
    width: auto;
    .link-video {
      iframe {
        width: 100%;
        height: 400px;
        border-radius: $radius-button;
      }
    }
    h3 {
      line-height: 2rem;
      padding: 1rem 0;
    }
  }
  .btn-webplayer {
    margin-top: 1rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    border-top: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
    button {
      background-color: rgba(255, 255, 255, 0);
      border: none;
      color: $primary;
      font-size: 1rem;
      font-weight: 600;
      line-height: 1.5rem;
      align-items: center;
      padding: 1rem 0.8rem;
      cursor: pointer;
    }
    .arrow {
      border: solid $primary;
      border-width: 0 3px 3px 0;
      display: inline-block;
      padding: 4px;
    }

    .right {
      transform: rotate(-45deg);
      -webkit-transform: rotate(-45deg);
    }

    .left {
      transform: rotate(135deg);
      -webkit-transform: rotate(135deg);
    }
  }
}

// responsive
@media only screen and (max-width: 1144px){
  .webplayer{
    .container{
      display: table-cell;
    }
  }
}
@media only screen and (max-width: 800px){
  .webplayer-wrapper {
    display: flex;
    flex-direction: column-reverse;
    .daftar-materi {
      .gambar-thumbnil {
        height: 120px;
      }}
      .btn-webplayer button {
        padding: 1rem 0;
      }
  }
}